import React, { useState } from 'react';
import styles from "./index.less";
import dayjs from "dayjs";
import { AutoCenter, Card, Grid } from "antd-mobile";

const format = "HH:mm";
const sections = [
    [dayjs('0000-00-00 08:00'), dayjs('0000-00-00 08:50')],
    [dayjs('0000-00-00 09:00'), dayjs('0000-00-00 09:50')],
    [dayjs('0000-00-00 10:10'), dayjs('0000-00-00 11:00')],
    [dayjs('0000-00-00 11:10'), dayjs('0000-00-00 12:00')],
    [dayjs('0000-00-00 14:30'), dayjs('0000-00-00 15:20')],
    [dayjs('0000-00-00 15:30'), dayjs('0000-00-00 16:20')],
    [dayjs('0000-00-00 16:30'), dayjs('0000-00-00 17:20')],
    [dayjs('0000-00-00 17:30'), dayjs('0000-00-00 18:20')],
];
const weeks = ['一', '二', '三', '四', '五', '六', '七'];
const courses = [{
    name: '高等数学1',
    teacher: '朱晓宁',
    room: 'YW310',
    color: 'blue',
}];
const schedules = [
    [{
        ...courses[0],
        start: 0,
        end: 1
    }], [], [], [], [], [], []
]

const CourseTable: React.FC = () => {

    const [width, setWidth] = useState<number>(0);
    const [height, setHeight] = useState<number>(0);

    return (
        <Grid columns={24} gap={6} className={styles.schedule}>
            <Grid.Item span={2}>
                <Grid.Item>
                    <Card>
                        <AutoCenter>
                            {dayjs().month() + 1}
                        </AutoCenter>
                    </Card>
                </Grid.Item>
                <br/>
                <Grid columns={1} gap={6}>
                    {sections.map((item, index) =>
                        <Grid.Item>
                            <div
                                ref={element => {
                                    if (element) {
                                        setHeight(element.getBoundingClientRect().height);
                                    }
                                }}
                            >
                                <Card>
                                        <AutoCenter>
                                            {index}
                                        </AutoCenter>
                                        <AutoCenter>
                                            {item[0].format(format)}
                                        </AutoCenter>
                                        <AutoCenter>
                                            {item[1].format(format)}
                                        </AutoCenter>
                                </Card>
                            </div>
                        </Grid.Item>
                    )}
                </Grid>
            </Grid.Item>
            <Grid.Item span={22}>
                <Grid columns={weeks.length} gap={6}>
                    {weeks.map(item =>
                        <Grid.Item>
                            <div
                                ref={element => {
                                    if (element) {
                                        setWidth(element.getBoundingClientRect().width);
                                    }
                                }}
                            >
                                <Card>
                                    <AutoCenter>
                                        {item}
                                    </AutoCenter>
                                </Card>
                            </div>
                        </Grid.Item>
                    )}
                </Grid>
                <br/>
                <Grid columns={weeks.length} gap={6}>
                    {schedules.map(schedule =>
                        <Grid.Item>
                            <Grid columns={1} gap={6}>
                                {schedule.map((item, index) => {
                                    const fix = 6.04;
                                    const style = {
                                        width: width,
                                        height: height * (item.end - item.start) * 2 + fix,
                                        background: item.color,
                                        marginTop: (height + fix) * (item.start - index * 2),
                                        wordWrap: 'break-word',
                                        padding: 0,
                                    };
                                    return (
                                        <Grid.Item>
                                            <Card style={style}>
                                                <div
                                                    style={{
                                                        transform: 'scale(0.864)',
                                                    }}
                                                >
                                                    {item.name}@{item.room}
                                                    <AutoCenter>
                                                        {item.teacher}
                                                    </AutoCenter>
                                                </div>
                                            </Card>
                                        </Grid.Item>
                                    );
                                })}
                            </Grid>
                        </Grid.Item>
                    )}
                </Grid>
            </Grid.Item>
        </Grid>
    );
};

export default CourseTable;
