import React from 'react';
import { Grid, Toast } from "antd-mobile";
import CheckCard from "./components/CheckCard";
import { CalendarDot, Optimize, Planet, Schedule } from "@icon-park/react";
import { useNavigate } from "react-router-dom";

const Workspace: React.FC = () => {

    const navigate = useNavigate();

    const applications = [{
        icon: <Schedule theme="outline" size="24" fill="#333"/>,
        name: '课程表',
        description: '本学期有13门课程',
        onClick: () => {
            navigate('/apps/courseTable');
        }
    }, {
        icon: <Optimize theme="outline" size="24" fill="#333"/>,
        name: '事项',
        description: '本学期有13门课程',
        onClick: () => {
            Toast.show('点击了卡片');
        }
    }, {
        icon: <CalendarDot theme="outline" size="24" fill="#333"/>,
        name: '日历',
        description: '本学期有13门课程',
        onClick: () => {
            Toast.show('点击了卡片');
        }
    }];

    return (
        <div
            style={{
                padding: 6.04
            }}
        >
            <Grid
                columns={2}
                gap={6}
            >
                {applications.map(item =>
                    <Grid.Item>
                        <CheckCard
                            {...item}
                        />
                    </Grid.Item>
                )}
            </Grid>
            <br />
            <Grid
                columns={1}
                style={{
                    padding: '0 25%'
                }}
            >
                <Grid.Item>
                    <CheckCard
                        icon={<Planet theme="outline" size="24" fill="#333"/>}
                        name="规划"
                        description="未来可期"
                        onClick={() => {}}
                    />
                </Grid.Item>
            </Grid>
        </div>
    );

};

export default Workspace;
